const styles = () => ({
  timeBlock: {
    backgroundColor: 'rgba(238, 238, 238, 0)',
    height: 'auto',
    position: 'relative',
    transition: 'all 2s ease-in-out',
    width: '100%',
    margin: '50px 0',
    willChange:'top, height'
  }
})

export default styles


